.pagination {
  margin-bottom: 0;

  .page-item {
    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  .page-link {
    display: flex;
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
    color: $white;
    background-color: #333;
    padding: 0;
    border: 0 !important;
    border-radius: 0 !important;
  }

  .page-item.disabled .page-link {
    background-color: #333;
    color: $white;
  }

  .page-item.active .page-link {
    background-color: $primary;
  }
}
