.news {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__frame {
    @include frame('rect', 70%);
    @include frame('zoom');
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    margin: 20px 0;
  }

  &__time {
    font-size: 12px;
    font-style: italic;
    margin-top: auto;
    margin-bottom: 20px;
  }

  &__desc {
    @include max-line(3);
  }
}

.project {
  display: flex;
  flex-direction: column;
  padding: 14px;
  border-radius: 4px;
  box-shadow: 0 10px 20px rgba(#000, 0.16);

  &__frame {
    @include frame('rect', 70%);
    @include frame('zoom');
  }

  &__title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    margin: 15px 0 0;
  }

  @media (max-width: 1599px) {
    &__title {
      font-size: 16px;
    }
  }

  @media (max-width: 1399px) {
    &__title {
      font-size: 14px;
    }
  }
}

.testimonial {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__content {
    flex-grow: 1;
    padding: 20px 30px;
    background-color: $primary;
    color: $white;
    position: relative;
    text-align: justify;

    &::after {
      content: '';
      border: 10px solid transparent;
      border-width: 10px 15px;
      border-color: $primary transparent transparent transparent;
      position: absolute;
      top: 100%;
      left: 45px;
      transform: translateX(-50%);
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 12px;
  }

  &__body {
    align-items: center;
    margin-top: 20px;
  }

  &__frame {
    @include frame('circle');
    width: 50px;
    margin: 0 20px;
  }

  &__name {
    font-size: 16px;
    font-weight: 700;
  }
}
