.banner-slider {
  position: relative;
  z-index: 1;

  &__frame {
    img {
      display: block;
      width: 100%;
    }
  }

  &__pagination {
    display: flex;
    position: absolute;
    bottom: 30px;
    left: 50%;
    z-index: 10;
    transform: translateX(-50%);

    .swiper-pagination-bullet {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      color: #555;
      border: 1px solid currentColor;
      padding: 4px;
      margin: 0 5px;
      opacity: 1;
      background: 0;

      &:focus {
        outline: 0;
      }

      &::before {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background-color: currentColor;
        border-radius: 50%;
      }

      &-active {
        color: $primary;
      }
    }
  }

  &__prev,
  &__next {
    padding: 20px 10px;
    position: absolute;
    top: 50%;
    left: 2%;
    z-index: 10;
    transform: translateY(-50%);
    cursor: pointer;
    font-size: 30px;
    color: $white;
    text-shadow: 0 1px 10px rgba(#000, 0.5);

    &:focus {
      outline: 0;
    }
  }

  &__next {
    left: auto;
    right: 2%;
  }

  @media (max-width: 767px) {
    &__pagination {
      bottom: 5px;

      .swiper-pagination-bullet {
        width: 12px;
        height: 12px;
        padding: 2px;
        margin: 0 2px;
      }
    }
  }
}

.testimonial-slider {
  position: relative;
  z-index: 1;

  .swiper-slide {
    height: auto;
  }
}

.category-slider {
  position: relative;
  z-index: 1;

  &__prev,
  &__next {
    padding: 10px 5px;
    position: absolute;
    top: 50%;
    left: -20px;
    z-index: 10;
    transform: translateY(-50%);
    font-size: 24px;
    cursor: pointer;

    &:focus {
      outline: 0;
    }

    &.swiper-button-disabled {
      display: none;
    }
  }

  &__next {
    left: auto;
    right: -20px;
  }
}
