.footer {
  padding: 40px 0;
  background-color: $light;

  &__logo {
    display: inline-flex;

    img {
      max-height: 34px;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    margin: 30px -15px 30px;
  }

  &__col {
    padding: 0 15px;

    &:not(:last-child) {
      border-right: 1px solid #ccc;
    }
  }

  &__contact {
    color: $secondary;

    p {
      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 8px;
    color: $secondary;
  }

  &__addr {
    color: $secondary;

    &::before {
      content: '';
      display: inline-block;
      width: 8px;
      height: 8px;
      background-color: $danger;
      border: 1px solid #000;
      margin-right: 8px;
      transform: rotate(45deg);
    }
  }

  &__copyright {
    text-align: center;
    color: $secondary;
  }

  @media (max-width: 991px) {
    &__grid {
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 30px 0;
    }

    &__col {
      &:nth-child(2) {
        grid-column: span 2;
      }
    }
  }

  @media (max-width: 575px) {
    &__grid {
      grid-template-columns: 1fr 1fr;
    }

    &__col {
      &:first-child {
        grid-column: span 2;
      }

      &:nth-child(2) {
        grid-column: span 1;
      }
    }
  }
}
