.home-about {
  &__title {
    font-size: 18px;
    text-transform: uppercase;
    margin-bottom: 8px;
  }
}

.intro {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 30px;

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;

    img {
      max-height: 100%;
      object-fit: contain;
      object-position: 50% 50%;
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
    margin: 15px 0;
  }

  &__desc {
    text-align: justify;
  }

  @media (max-width: 991px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media (max-width: 575px) {
    grid-template-columns: 100%;

    &__col {
      position: relative;
      padding-left: 70px;
    }

    &__icon {
      width: 50px;
      position: absolute;
      top: 0;
      left: 0;
    }

    &__title {
      font-size: 16px;
      text-align: left;
      margin: 0 0 8px;
    }
  }
}

.categories {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: -8px -15px;

  &__item {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    margin: 8px 15px;
    flex: 0 0 calc(20% - 30px);
    max-width: calc(20% - 30px);
  }

  &__frame {
    @include frame('rect', 70%);

    img {
      &:nth-child(2) {
        opacity: 0;
        transition: 0.4s;
      }
    }
  }

  &__overlay {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: rgba(#000, 0.5);
    color: $white;
    padding: 4px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    transition: 0.4s;

    &:hover {
      color: $white;
      text-decoration: none;
    }
  }

  &__item:hover &__frame img:nth-child(2) {
    opacity: 1;
  }

  &__item:hover &__overlay {
    opacity: 0;
  }

  @media (max-width: 991px) {
    margin: -8px -10px;

    &__item {
      flex: 0 0 calc(33.33% - 20px);
      max-width: calc(33.33% - 20px);
      margin: 8px 10px;
    }
  }

  @media (max-width: 575px) {
    &__item {
      flex: 0 0 calc(50% - 20px);
      max-width: calc(50% - 20px);
    }
  }
}

