.header {
  position: relative;
  z-index: 100;
  height: 62px;

  &__wrapper {
    width: 100%;
    background-color: $white;
    @include shadow('xs');
    position: fixed;
    top: 0;
    left: 0;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  &__logo {
    display: inline-flex;
    margin: 16px 0;

    img {
      height: 30px;
    }
  }

  &__elements {
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__btn {
    display: inline-flex;
    flex-direction: column;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    border: 0;
    background: 0;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);

    span {
      width: 26px;
      height: 1px;
      background-color: $primary;
      margin: 3px 0;
    }
  }

  &__slogan {
    display: none;
  }

  @media (min-width: 1200px) {
    height: auto;

    &__wrapper {
      position: static;
      box-shadow: unset;
    }

    &__logo {
      margin: 15px 0;

      img {
        height: 42px;
      }
    }

    &__slogan {
      display: block;
      text-align: center;
      margin-bottom: 10px;
    }

    &__btn {
      display: none;
    }
  }
}

.logo {
  display: inline-flex;
  margin: 16px 0;

  img {
    height: 30px;
  }
}

.search-btn {
  display: flex;
  width: 35px;
  height: 35px;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  border: 0;
  background: 0;
  font-size: 20px;
  color: $primary;
  cursor: pointer;

  @media (min-width: 1200px) {
    height: 40px;
    position: absolute;
    z-index: 20;
    top: 100%;
    right: 0;
    margin-top: 1px;
    transform: translateY(0);
  }
}

.search {
  display: none;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  padding: 10px 0;
  background-color: $primary;

  &::before {
    content: '';
    display: block;
    width: 100vw;
    height: 100%;
    background-color: $primary;
    position: absolute;
    top: 0;
    left: 50%;
    z-index: -1;
    transform: translateX(-50%);
    @include shadow('xs');
  }

  .form-control {
    border: 0;
  }

  .input-group-text {
    background-color: $white;
    border: 0;
  }

  @media (min-width: 1200px) {
    display: block;
    width: 350px;
    padding: 10px;
    top: calc(100% + 41px);
    left: auto;
    right: 0;
    @include shadow('xs');

    &::before {
      display: none;
    }
  }
}

.navigation {
  @media (min-width: 1200px) {
    border-top: 1px solid $border-color;

    &__inner {
      position: relative;
      z-index: 10;
    }
  }
}
