.button {
  @include btn('shine');
  display: inline-block;
  padding: 0 24px;
  font-size: 12px;
  line-height: 30px;
  border-radius: 4px;
  background-color: $primary;
  color: $white;
  text-transform: uppercase;

  &:hover {
    color: $white;
    text-decoration: none;
  }
}

.advice {
  padding: 8px 15px;
  border-radius: 4px;
  background-color: $primary;

  &__title {
    font-size: 14px;
    font-weight: 600;
    text-align: center;
    color: $white;
    margin-bottom: 8px;
  }

  &__btn {
    display: inline-block;
    color: $secondary;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 35px;
    border-radius: 4px;
    background-color: $white;
    border: 0;
    padding: 0 40px;
  }

  .form-group {
    margin-bottom: 8px;
  }

  .form-control {
    border: 0;
  }

  &--custom {
    padding: 24px 15px;
  }

  &--custom &__title {
    font-size: 30px;
    margin-bottom: 24px;
    text-transform: uppercase;
  }

  &--custom .form-group {
    margin-bottom: 15px;
  }

  @media (max-width: 991px) {
    &--custom {
      padding: 20px 15px;
    }

    &--custom &__title {
      font-size: 20px;
      margin-bottom: 20px;
    }
  }

  @media (max-width: 767px) {
    &--custom &__title {
      font-size: 16px;
    }
  }
}

.register {
  background-color: $primary;
  margin-bottom: 30px;
  padding: 15px;
  border-radius: 4px;

  &__title {
    font-size: 14px;
    font-weight: 700;
    color: $white;
    text-transform: uppercase;
    border-bottom: 1px solid $white;
    padding-bottom: 8px;
    margin-bottom: 20px;
    text-align: center;
  }

  &__btn {
    display: inline-block;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    color: $secondary;
    padding: 0 20px;
    line-height: 35px;
    border-radius: 5px;
    border: 0;
    background-color: $white;
  }

  label {
    color: $white;
  }

  .form-control,
  .custom-select {
    border: 0;
  }
}
