.about {
  padding: 30px 0;

  &+& {
    border-top: 1px solid $border-color;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 30px;
  }

  &__content {
    text-align: center;
  }
}

.about-feature {
  &__title {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    margin: 0 0 16px;
  }
}

.about-video {
  margin-top: 30px;
  background: url('../images/about-video-bg.png') center/contain no-repeat;

  &__inner {
    max-width: 360px;
    margin: 0 auto;
  }
}
