.preview {
  &__thumbs {
    margin-top: 10px;
  }
}

.preview-slider {
  &__frame {
    @include frame('rect', 56.25%);
  }
}

.thumb-slider {
  position: relative;

  &__frame {
    @include frame('rect', 56.25%);
  }

  &.has-nav {
    padding: 0 20px;
  }

  &.has-nav &__prev,
  &.has-nav &__next {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 100%;
    background-color: #ddd;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;

    &:focus {
      outline: 0;
    }
  }

  &.has-nav &__next {
    left: auto;
    right: 0;
  }

  &:not(.has-nav) &__prev,
  &:not(.has-nav) &__next {
    display: none;
  }



  &__pagination {
    display: flex;
    justify-content: center;
    padding: 20px 0;

    .swiper-pagination-bullet {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      color: #555;
      border: 1px solid currentColor;
      padding: 4px;
      margin: 0 5px;
      opacity: 1;
      background: 0;

      &:focus {
        outline: 0;
      }

      &::before {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background-color: currentColor;
        border-radius: 50%;
      }

      &-active {
        color: $primary;
      }
    }
  }

  @media (max-width: 767px) {
    &__pagination {
      padding: 10px 0;

      .swiper-pagination-bullet {
        width: 12px;
        height: 12px;
        padding: 2px;
        margin: 0 2px;
      }
    }
  }
}
