.filter {
  background-color: $light;
  padding: 15px;

  &__list {
    display: block;
    overflow: auto;
    white-space: nowrap;

    .form-group {
      display: inline-block;
      margin-bottom: 0;

      &:not(:last-child) {
        margin-right: 10px;
      }
    }

    .custom-select {
      height: 35px;
      padding: 6px 22px 6px 6px;
      background-position: 94% 50%;
      font-size: 12px;
      line-height: 21px;
    }
  }

  &__search {
    width: 200px;
    margin-left: 20px;

    .form-control {
      border: 0;
      font-size: 12px;
      height: 35px;
    }

    .input-group-text {
      background-color: $white;
      border: 0;
      color: $primary;
      font-size: 12px;
    }
  }

  @media (max-width: 991px) {
    flex-direction: column;

    &__search {
      margin-top: 8px;
      margin-left: auto;
    }
  }
}

.pj-info {
  margin: 15px 0 20px;

  table {
    tbody {
      td {
        background-color: #E8E9EB;
        padding: 10px 24px;
        vertical-align: middle;
        border-color: $white;

        &:first-child {
          color: $white;
          background-color: #BC7026;
          white-space: nowrap;
        }
      }
    }
  }

  @media (max-width: 767px) {
    table {
      tbody {
        td {
          padding: 8px 10px;
        }
      }
    }
  }
}

.pj-detail {
  &__title {
    font-size: 36px;
    font-weight: 600;
    line-height: 1.2;
    margin-bottom: 16px;
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 24px;
    }
  }
}
