.section {
  &--custom {
    border-bottom: 1px dashed #ccc;
    padding-bottom: 40px;
    margin-bottom: 40px;
  }

  &__header {
    padding-bottom: 26px;
    position: relative;
    margin-bottom: 30px;

    &::after {
      content: '';
      display: block;
      width: 554px;
      max-width: 100%;
      height: 19px;
      background: url('../images/title-underline.png') center/contain no-repeat;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__title {
    font-size: 40px;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 0;
    color: $primary;
  }

  &__desc {
    font-style: italic;
    text-align: center;
    margin: 10px 0;
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 34px;
    }
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 26px;
    }
  }
}

.aside {
  &__title {
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
    color: $primary;
  }
}
